.header_container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px 20px 10px 15px;
    align-items: end;
    border-bottom: 1px solid #f0f0f0;
    .welcome_message {
        font-size: 17px;
        font-weight: 500;
        color: #292D32;
        flex: 1;
    }
    .notification {
        position: relative;
        margin-right: 6%;
        margin-bottom: 10px;
        cursor: pointer;
        svg {
            color: #909090;
            font-size: 20px;
            width: 20px;
            height: 20px;
        }
        .notification_alert {
            width: 7px;
            height: 7px;
            border: 1px solid #fff;
            box-sizing: content-box;
            border-radius: 50%;
            background-color: #EB5757;
            position: absolute;
            top: -1px;
            right: -1px;
        }
    }
    .profile_container {
        padding: 8px;
        border-radius: 10px;
        background: #ebf5ff;
        display: flex;
        align-items: center;
        gap: 10px;
        .profile_pic {
            width: 40px;
            height: 40px;
            img {
                width: 100%;
                height: 100%;
                border-radius: 6px;
                object-fit: cover;
            }
        }
        .profile_details {
            .name {
                color: #303030;
                font-size: 14px;
                font-weight: 400;
            }
            .desc {
                font-size: 12px;
                font-weight: 400;
                color: #369FFF;
                display: flex;
                gap: 10px;
                align-items: center;
                svg {
                    font-size: 14px;
                    color: #303030;
                }
            }
        }
    }
}

.profile_menu {
    .MuiPaper-root {
        top: 80px !important;
        right: 35px !important;
        left: auto !important;
    }
}