.bus_button {
    width: 100px;
    height: 40px;
    background-color: var(--primary);
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: #fff;
    border-radius: 30px;
    text-transform: none;
    border: none;
    cursor: pointer;
    margin: 20px 20px 10px;
}


.table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.th, .td {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

.th {
    background-color: #f2f2f2;
}

.tr:nth-child(even) {
    background-color: #f9f9f9;
}



