.busHeader {
  display: flex;
  align-items: center;
  gap: 26px;
  h1 {
    color: var(--Black-font, #303030);
    font-family: Manrope;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  button {
    padding: var(--24, 24px);
    border-radius: var(--16, 16px);
    background: #f1bf31;
    border: none;
    padding: 12px;

    color: var(--black-100, #1c1c1c);
    font-feature-settings: "cv11" on, "cv01" on, "ss01" on;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
  }
}

.tripDetails {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 28px;
  .busCard {
    display: grid;
    grid-template-columns: 70% 30%;
    width: 202px;
    min-width: 200px;
    padding: var(--24, 24px);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--8, 8px);
    border-radius: var(--16, 16px);
    background: var(--primary-blue, #e3f5ff);
    .icon {
      display: flex;
      flex-direction: row;
      align-items: end;
      height: 50px;
    }
  }
}

.subHeader {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  margin-bottom: 40px;
  h1 {
    color: var(--Black-font, #303030);
    font-family: Manrope;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
}

.busCard p:nth-child(2) {
  font-size: 24px;
  font-weight: 600;
}

.busCard p:nth-child(1) {
  font-size: 24px;
  font-weight: 600;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
}

.busTile {
  
  border-radius: 16px;
  display: grid;
  padding: 20px;
  grid-template-columns: 60px 1fr 50px 1fr auto 20px;
  cursor: pointer;
  background: #F7F7F7;
  margin-bottom: 12px;
  .driverInfo p:nth-child(1) {
    color: var(--Black-font, #303030);
    font-family: Lato;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.08px;
  }
  .driverInfo p:nth-child(2) {
    color: #000;
    font-family: Lato;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.08px;
  }
  .driverInfo p:nth-child(3) {
    color: var(--Gray-4, #bdbdbd);
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  .busTile2 p:nth-child(1) {
    color: #000;
    font-family: Nunito Sans;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.08px;
  }
  .busTile2 p:nth-child(2) {
    color: #8c8c8c;
    font-family: Lato;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 133.333% */
    letter-spacing: -0.24px;
  }

  .busTile2 p:nth-child(2) {
    color: var(--Gray-4, #bdbdbd);
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
}

.MuiDialogTitle-root {
  border-bottom: 1px solid #bfbfbf;
  box-shadow: 0 0px 8px rgba(0, 0,0, .15);
}
.MuiDialogActions-root {
  padding-top: 10px;
  border-top: 1px solid #dfdfdf;
  .bus_button {
    width: 100px;
    height: 40px;
    background-color: var(--primary);
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: #fff;
    border-radius: 30px;
    text-transform: none;
    border: none;
    cursor: pointer;
  }
}

.menu{
    font-size: 24px;
    height: 14px;
    display: flex   ;

}