* {
  font-family: "Manrope", sans-serif;
}

.blue {
  background: #369fff;
  box-shadow: 0px 10px 30px 0px rgba(54, 159, 255, 0.4);
  border-radius: 20px;
  background: #369fff;
}

.orange {
  border-radius: 20px;
  background: #ff993a;
  box-shadow: 0px 10px 30px 0px rgba(255, 153, 58, 0.4);
}

.green {
  border-radius: 20px;
  background: #8ac53e;
  box-shadow: 0px 10px 30px 0px rgba(138, 197, 62, 0.4);
}

.yellow {
  border-radius: 20px;
  background: #ffd143;
  box-shadow: 0px 10px 30px 0px rgba(255, 209, 67, 0.4);
}

.buses {
  border-radius: 20px;
  min-height: 150px;
  display: flex;
  justify-content: space-between;
  padding: 20px;
  cursor: pointer;

  box-shadow: 0px 10px 30px 0px rgba(54, 159, 255, 0.4);

  p {
    color: var(--white, var(--white-100, #fff));
    font-family: Manrope;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    position: relative;
    bottom: 40px;
  }
}

.dashGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
}
