* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #F1BF31;
}

.flex {
  display: flex;
}
.align-start {
  align-items: start;
}
.align-center {
  align-items: center;
}
.align-end {
  align-items: end;
}
.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between;
}
.justify-around {
  justify-content: space-around;
}
.loader_backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgb(6 6 6 / 37%);
  display: flex;
  gap: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  .loader {
    display: flex;
    align-items: end;
    gap: 15px;
    padding: 30px;
    background: #fff;
    border-radius: 10px;
  }
}
.loader span {
  font-size: 17px;
  font-weight: 500;
}
.loader * {
  color: var(--primary);
}