.sidenav_container {
    width: 100%;
    padding: 25px 20px 10px;
    min-height: 100vh;
    border-right: 2px solid #f0f0f0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .logo_container {
        h5 {
            font-size: 20px;
            font-weight: 700;
            color: var(--primary);
            font-family: 'Nunito Sans', sans-serif;
        }
    }
    .menu_container {
        margin-top: 15px;
        .menus {
            list-style: none;
            .sidemenu {
                font-size: 14px;
                font-weight: normal;
                color: #bdbdbd;
                line-height: 32px;
                display: flex;
                align-items: center;
                gap: 10px;
                cursor: pointer;
                width: fit-content;
                padding-right: 30px;
                &.active {
                    color: var(--primary);
                    gap: 20px;
                    position: relative;
                    &::after {
                        content: '';
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;
                        background: var(--primary);
                        position: absolute;
                        top: calc(50% - 4px);
                        right: 0;
                    }
                }
                svg {
                    width: 20px;
                }
            }
        }
    }
    .ticket_container {
        .ticket_img {
            img {
                margin-bottom: -45px;
                margin-left: 15px;
                height: 165px;
            }
        }
        .raise_ticket {
            padding: 50px 20px 20px;
            color: #369FFF;
            background-color: #ebf5ff;
            border-radius: 20px;
            display: flex;
            font-size: 15px;
            font-weight: 500;
            justify-content: space-between;
            align-items: end;
            span {
                width: 55px;
            }
            button {
                padding: 7px;
                border: 1px solid #369FFF;
                color: #369FFF;
                font-size: 16px;
                border-radius: 10px;
                width: 34px;
            }
        }
    }
}