
table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

th, td {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

th {
    background-color: #f2f2f2;
}

tr:nth-child(even) {
    background-color: #f9f9f9;
}

.form_control {
    
    padding: 10px;
    box-sizing: border-box;
    flex-direction: column;
    display: flex;
    label {
        font-size: 14px;
        margin-bottom: 4px;
        margin-left: 10px;
        font-weight: 500;
    }
    .upload {
        width: 80%;
        background-color: #d9d9d9;
        color: #1c1c1c;
        padding: 7px 15px;
        border-radius: 20px;
        position: relative;
        display: flex;
        gap: 8px;
        span {
            flex: 1;
            font-size: 12px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
        svg {
            z-index: 10;
            font-size: 14px;
            cursor: pointer;
            &.remove {color: #ed0000;}
        }
    }
    .upload, .upload_block {
        .MuiInputBase-root {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100% !important;
            opacity: 0;
        }
    }
    .upload_block {
        width: 60px;
        height: 50px;
        background-color: #d9d9d9;
        color: #1c1c1c;
        border-radius: 6px;
        position: relative;
        padding: 5px;
        font-size: 20px;
        .uploaded {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 7px;
            svg {
                z-index: 10;
                font-size: 15px;
                cursor: pointer !important;
                &:nth-last-child(1) {color: #ed0000;}
            }
        }
    }
    &.file_upload {
        .MuiInputBase-root {border: none !important;}
    }
    .MuiFormControl-root {
        width: 100%;
    }
    .MuiInputBase-root {
        height: 44px !important;
    }
    .MuiOutlinedInput-notchedOutline {
        border-color: #ddd !important;
        border-width: 1px !important;
        border-radius: 35px;
    }
    // .MuiInputAdornment-root {color: #ddd;}
    input {
        padding: 10px 15px !important;
        font-size: 14px !important;
        color: #333;
        
        // &::placeholder {color: #ddd;}
    }
}

.addDriverFields{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.add_button {
    width: 100px;
    height: 40px;
    background-color: var(--primary) !important;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: #fff !important;
    border-radius: 30px !important ;
    text-transform: none;
    border: none;
    cursor: pointer;
    margin: 20px 60px 10px auto;
}

.MuiOutlinedInput-notchedOutline {
    border-color: #ddd !important;
    border-width: 1px !important;
    border-radius: 35px;
}