.add_route_container {
    width: 100%;
    .add_route_form {
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 10px;
        .form_control {
            width: 33.33%;
            padding: 10px;
            box-sizing: border-box;
            flex-direction: column;
            display: flex;
            label {
                font-size: 14px;
                margin-bottom: 4px;
                margin-left: 10px;
                font-weight: 500;
            }
            .MuiFormGroup-root {
                flex-direction: row;
                & ~ .MuiFormControl-root .MuiInputBase-root {display: none;}
            }
            .MuiFormControlLabel-root {
                margin-left: 0;
                margin-right: 0;
            }
            .MuiFormControl-root {
                width: 100%;
            }
            .MuiInputBase-root {
                height: 44px !important;
                padding: 0;
            }
            .MuiOutlinedInput-notchedOutline {
                border-color: #ddd !important;
                border-width: 1px !important;
                border-radius: 35px;
            }
            // .MuiInputAdornment-root {color: #ddd;}
            input {
                padding: 10px 15px !important;
                font-size: 14px !important;
                color: #333;
                
                // &::placeholder {color: #ddd;}
            }
            textarea {
                border: 1px solid rgb(228, 229, 233);
                border-radius: 20px;
                padding: 10px 15px;
                outline: none;
                font-size: 14px !important;
                color: #333;
            }
        }
    }
    .add_stop_container {
        width: 100%;
        .add_stop_btn {
            padding: 5px 10px;
            color: #fff;
            background-color: #1c1c1c;
            border-radius: 25px;
            font-size: 12px;
            display: block;
            margin: 0 10px 0 auto;
        }
        h5 {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 18px;
            font-weight: 600;
            padding: 10px 5px;
            width: 100%;        
        }
        .add_route_form {
            padding-top: 10px;
            border-top: 1px solid #e4e5e9;
        }
    }
    .address {
        width: 100%;
        display: flex;
        align-items: center;
        .address_form {
            width: 66.666%;
            display: flex;
            flex-wrap: wrap;
            .form_control {width: 50%;}
        }
        .from_map_container {
            flex: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            .from_map {
                padding: 10px;
                border: 1px solid #e4e5e9;
                border-radius: 5px;
                display: flex;
                flex-direction: column;
                align-items: center;
                font-size: 13px;
                gap: 5px;
                cursor: pointer;
                svg {
                    width: 60px;
                    height: 60px;
                }
            }
        }
    }
    .bus_button {
        width: 100px;
        height: 40px;
        background-color: var(--primary);
        text-align: center;
        font-size: 14px;
        font-weight: 400;
        color: #fff;
        border-radius: 30px;
        text-transform: none;
        border: none;
        cursor: pointer;
        margin: 50px auto 10px;
        display: block;
    }
}

.choose_map_dialog {
    .MuiPaper-root {
        margin: 0 !important;
        top: 0 !important;
        right: 0 !important;
        max-height: 100% !important;
    }
}