.bus_details_container {
    width: 100%;
    .details_container {
        width: 100%;
        padding: 15px 20px;
        border-bottom: 1px solid #f0f0f0;
        gap: 50px;
        .bus_details {
            svg.bus_icon {
                width: 45px;
                height: 45px;
            }
            .bus_number {
                font-size: 18px;
                font-weight: 500;
                color: #000;
            }
            .bus_desc {
                color: #333;
                font-size: 14px;
                font-weight: 400;
            }
            .actions {
                margin-top: 15px;
                list-style: none;
                gap: 6px;
                .action {
                    width: 28px;
                    height: 28px;
                    border-radius: 5px;
                    background: #F0F2F5;
                    cursor: pointer;
                    svg {
                        width: 15px;
                        height: 15px;
                    }
                }
            }
        }
        .filters_container {
            flex: 1;
            .filters {
                list-style: none;
                .filter {
                    gap: 10px;
                    .filter_icon {
                        width: 32px;
                        height: 32px;
                        border-radius: 5px;
                        background-color: #E9AD1D;
                        svg {
                            width: 16px;
                            height: 16px;
                            color: #fff;
                        }
                    }
                    &:nth-child(2) .filter_icon {background-color: #629CF4;}
                    &:nth-child(3) .filter_icon {background-color: #E05C64;}
                    &:nth-child(4) .filter_icon {background-color: #8956F8;}
                    &:nth-child(5) .filter_icon {background-color: #8956F8;}
                    &:nth-child(6) .filter_icon {background-color: #F19100;}
                    .filter_detail {
                        font-weight: 400;
                        .type {
                            font-size: 12px;
                            color: #161B25;
                        }
                        .value {
                            font-size: 11px;
                            color: #CFD5E2;
                        }
                    }
                }
            }
        }
    }
    .contact_trip_container {
        width: 100%;
        display: flex;
        .contact_details_container {
            width: 300px;
            border-right: 1px solid #f0f0f0;
            h5.heading {
                font-size: 14px;
                font-weight: 600;
                color: #161b25;
                gap: 6px;
                svg {
                    width: 14px;
                    height: 14px;
                    color: #6F7277;
                    margin-top: 2px;
                }
            }
            .view_all {
                font-size: 13px;
                font-weight: 400;
                color: #369FFF;
                cursor: pointer;
            }
            .about_this_contact, .tags_container, .maintanance_container, .fuel_history_container {
                width: 100%;
                padding: 15px 10px;
                border-bottom: 2px solid #f0f0f0;
            }
            .about_this_contact {
                .contact_details {
                    padding: 15px 0 10px 5px;
                    .detail {
                        padding: 5px 10px;
                        svg {
                            width: 12px;
                            height: 12px;
                            margin-right: 10px;
                        }
                        p {
                            font-size: 14px;
                            font-weight: 500;
                        }
                        .label {
                            color: #6F7277;
                            margin-right: 5px;
                        }
                        .value {
                            color: #161b25;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            cursor: default;
                        }
                        .desc {
                            font-size: 12px;
                            font-weight: 400;
                            color: #CFD5E2;
                            margin-left: auto;
                        }
                    }
                }

            }
            .tags_container {
                .tags {
                    list-style: none;
                    padding: 15px 10px 0;
                    gap: 10px;
                    flex-wrap: wrap;
                    .tag {
                        padding: 4px 10px;
                        border: 1px solid #f0f0f0;
                        border-radius: 20px;
                        font-size: 12px;
                        font-weight: 600;
                        background-color: rgba(137, 86, 248, 0.1);
                        color: rgb(137, 86, 248);
                        border: 1px solid rgba(137, 86, 248, 0.2);
                        cursor: pointer;
                        &.active {
                            background-color: rgba(137, 192, 18, 0.3);
                            color: rgb(47, 166, 6);
                            border: 1px solid rgba(243, 59, 18, 0.1);
                        }
                    }
                }
            }
            .maintanance_container {
                padding-bottom: 0;
                .maintanance_status {
                    .status {
                        padding: 4px 6px;
                        border-radius: 6px;
                        background-color: rgb(2, 201, 79);
                        color: #fff;
                        font-size: 12.5px;
                        font-weight: 500;
                        gap: 5px;
                        white-space: nowrap;
                    }
                    .ref_id {
                        font-size: 13px;
                        font-weight: 500;
                        color: rgba(111, 114, 119, 1);
                        span  {
                            color: #161B25;
                        }
                    }
                }
                .maintanance_list {
                    list-style: none;
                    .maintanance {
                        gap: 8px;
                        padding: 15px 0;
                        border-bottom: 1px solid #f0f0f0;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 1.5;
                        svg {
                            width: 13px;
                            height: 13px;
                            color: rgb(2, 201, 79);
                            margin-top: 4px;
                        }
                        p.type {
                            color: #6F7277;
                            span {
                                font-weight: 800;
                                color: #161b25;
                            }
                        }
                    }
                }
            }
            .fuel_history_container {
                padding-bottom: 0;
                .fuel_history_list {
                    list-style: none;
                    padding: 10px 0 0 12px;
                    .fuel_history {
                        gap: 8px;
                        padding: 15px 0;
                        border-bottom: 1px solid #f0f0f0;
                        font-size: 12px;
                        font-weight: 400;
                        line-height: 1.5;
                        svg {
                            width: 13px;
                            height: 13px;
                            color: rgb(2, 201, 79);
                            margin-top: 4px;
                        }
                        p {
                            color: #6F7277;
                            &.quantity {
                                font-weight: 700;
                                span {
                                    font-weight: 500;
                                }
                            }
                            &.fuel_at {
                                color: #161B25;
                            }
                            &.fuel_ref {
                                span {color: #161b25;}
                            }
                        }
                    }
                }
            }
        }
        .trips_container {
            flex: 1;
            background-color: rgba(240, 242, 245, 0.5);
            padding: 25px 20px;
            .trip_filter_container {
                width: 100%;
                .trip_filter {
                    list-style: none;
                    gap: 10px;
                    li {
                        padding: 4px 10px;
                        border-radius: 20px;
                        color: #6F7277;
                        font-size: 13px;
                        font-weight: 500;
                        cursor: pointer;
                        &.active {
                            color: #fff;
                            background-color: #1755E7;
                        }
                    }
                }
            }
            .recent_trips_container {
                .recent_trip_heading {
                    width: 100%;
                    h5 {
                        font-size: 16px;
                        font-weight: 700;
                        color: #161b25;
                    }
                    .view_all {
                        font-size: 12px;
                        font-weight: 500;
                        padding: 4px 10px;
                        border-radius: 5px;
                        color: #1755E7;
                        border: 1px solid #C3CDD9;
                        text-decoration: none;
                    }
                }
                .recent_trips {
                    margin-top: 10px;
                    margin-right: 20px;
                    .trip_card {
                        min-width: 350px;
                        padding: 15px;
                        background-color: #fff;
                        .trip_date {
                            color: #000;
                            flex-direction: column;
                            line-height: 1.1;
                            .month {
                                font-size: 13px;
                                font-weight: 500;
                            }
                            .date {
                                font-size: 26px;
                                font-weight: 500;
                            }
                            .week {
                                font-size: 13px;
                                font-weight: 500;
                                color: #CFD5E2;
                            }
                        }
                        .divider {
                            height: 40px;
                            width: 2px;
                            margin: 0 10px;
                            background-color: #f0f0f0;
                        }
                        .trip_details {
                            flex: 1;
                            .trip_to, .trip_by {
                                font-size: 14px;
                                font-weight: 500;
                                line-height: 1.5;
                            }
                            .trip_to {
                                color: #6F7277;
                            }
                            .trip_by {
                                color: #161b25;
                            }
                            .trip_status {
                                padding: 3px 6px;
                                border-radius: 20px;
                                font-size: 11px;
                                font-weight: 500;
                                &.delay {
                                    background-color: rgba(243, 59, 18, 0.1);
                                    border: 1px solid rgba(243, 59, 18, 0.2);
                                    color: #F33B12;
                                }
                                &.onTime {
                                    background-color: rgba(2, 201, 79, 0.1);
                                    border: 1px solid rgba(2, 201, 79, 0.2);
                                    color: #02C94F;
                                }
                            }
                            .delay_reason, .on_time {
                                font-size: 12px;
                                font-weight: 400;
                                color: #CFD5E2;
                                line-height: 1.6;
                                span {
                                    color: #161b25;
                                }
                            }
                        }
                    }
                }
            }
            .route_history_container {
                width: 100%;
                .route_date {
                    margin: auto;
                    width: fit-content;
                    padding: 4px 15px;
                    background-color: rgba(195, 205, 217, 0.5);
                    border-radius: 20px;
                    font-size: 13px;
                    font-weight: 500;
                    color: #161b25;
                }
                .route_history {
                    width: 75%;
                    margin: 10px auto;
                    list-style: none;
                    .route {
                        gap: 20px;
                        padding-bottom: 25px;
                        position: relative;
                        &:nth-last-child(1) {
                            &::before {display: none;}
                        }
                        &::before {
                            content: '';
                            width: 1px;
                            height: calc(100% - 24px);
                            border-left: 1px dashed rgba(195, 205, 217, 0.5);
                            position: absolute;
                            top: 24px;
                            left: 12px;
                        }
                        svg.marker {
                            font-size: 24px;
                        }
                        .route_card {
                            flex: 1;
                            background-color: #fff;
                            padding: 15px;
                            .stop {
                                line-height: 1.5;
                                .stop_no, .stop_name {
                                    font-size: 14px;
                                    font-weight: 500;
                                }
                                .stop_no {
                                    color: #6F7277;
                                    &.delay {color: #F33B12;}
                                    &.onTime {color: #02C94F;}
                                }
                                .stop_name {color: #161b25;}
                                .updated_on {
                                    font-size: 12px;
                                    font-weight: 400;
                                    color: #CFD5E2;
                                }
                            }
                            .stop_time {
                                font-size: 14px;
                                font-weight: 500;
                                color: #6F7277;
                                line-height: 1.5;
                                span {color: #161b25;}
                                svg {
                                    font-size: 5px;
                                    color: #d9d9d9;
                                }
                            }
                            .stop_note {
                                margin-top: 10px;
                                .note_heading {
                                    font-size: 13px;
                                    font-weight: 500;
                                    color: #CFD5E2;
                                }
                                .note {
                                    margin-top: 5px;
                                    padding: 8px 10px;
                                    font-size: 14px;
                                    font-weight: 500;
                                    color: #161b25;
                                    border-radius: 8px;
                                    background-color: rgba(240, 242, 245, 0.5);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}