.register_container {
    width: 100vw;
    height: 100vh;
    display: flex;
    .register_banner {
        height: 100%;
        width: auto;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: var(--primary);
        padding-top: 65px;
        .banner_title {
            font-size: 64px;
            font-weight: 700;
            color: #FFF;
            font-family: 'Nunito Sans', sans-serif;
            margin-bottom: -150px;
            z-index: 2;
        }
        .register_banner_img {
            // width: 400px;
            // height: 400px;
            width: 70%;
        }
    }
    .register_content {
        width: 500px;
        padding: 20px;
        padding-left: 40px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        .register_hello {
            font-size: 26px;
            font-weight: 700;
            margin: 0;
        }
        .register_welcome {
            font-size: 18px;
            font-weight: 400;
            color: #1c1c1c;
            margin: 5px 0 25px;
        }
        .register_form {
            width: 300px;
            .form_control {
                width: 100%;
                margin-bottom: 10px;
                .MuiFormControl-root {
                    width: 100%;
                }
                .MuiInputBase-root {
                    height: 50px !important;
                }
                .MuiOutlinedInput-notchedOutline {
                    border-color: #ddd !important;
                    border-width: 1px !important;
                    border-radius: 35px;
                }
                .MuiInputAdornment-root {color: #ddd;}
                input {
                    padding: 15px 15px 15px 5px !important;
                    font-size: 14px !important;
                    color: #333;
                    // &::placeholder {color: #ddd !important;}
                }
            }
            .bus_button {
                width: 300px;
                background-color: var(--primary);
                text-align: center;
                font-size: 14px;
                font-weight: 400;
                color: #fff;
                height: 50px;
                border-radius: 30px;
                text-transform: none;
            }
            .forget_password {
                font-size: 13px;
                text-decoration: none;
                color: #676767;
                text-align: center;
                width: 100%;
                display: block;
                margin-top: 10px;
            }
        }
    }
}

@media (max-width: 1280px) {
    .register_container {
        .register_banner {
            .banner_title {
                margin-bottom: -80px;
            }
        }
    }
}

@media (max-width: 996px) {
    .register_container {
        .register_banner {
            .banner_title {
                margin-bottom: -50px;
                font-size: 56px;
            }
        }
    }
}

@media (max-width: 876px) {
    .register_container {
        .register_banner {
            .banner_title {
                margin-bottom: -30px;
                font-size: 48px;
            }
        }
    }
}

@media (max-width: 786px) {
    .register_container {
        .register_banner {
            .banner_title {
                margin-bottom: -10px;
                font-size: 32px;
            }
        }
    }
}

@media (max-width: 696px) {
    .register_container {
        .register_banner {
            display: none;
        }
        .register_content {
            width: 100%;

        }
    }
}